import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import "./Timer";
import "./App.scss";
import {
  activateTimer,
  updateTitle,
  updateMinute,
  updateTerm
} from "./actions";
import Timer from "./Timer";

const App = () => {
  const dispatch = useDispatch();
  const { activeTimer, term, title, minute } = useSelector(state => state);
  return (
    <div className="App">
      <div className="container">
        <Timer />
        <input
          className="input input__title"
          type="text"
          value={title}
          placeholder="What do you do?"
          onInput={e => dispatch(updateTitle(e.target.value))}
          hidden={activeTimer}
        ></input>
        <input
          className="input input__minute"
          type="text"
          value={minute}
          placeholder="Minutes?"
          onInput={e => dispatch(updateMinute(e.target.value))}
          hidden={activeTimer}
        ></input>
        <button
          className="button__start"
          onClick={() => {
            dispatch(activateTimer(true));
            dispatch(
              updateTerm(
                moment().format("hh:mm") +
                  " ~ " +
                  moment()
                    .add(minute ? minute : 30, "minutes")
                    .format("hh:mm")
              )
            );
          }}
          hidden={activeTimer}
        >
          Start
        </button>
        <div className="title_container">
          <div className="title" hidden={!activeTimer}>
            {title}
          </div>
        </div>
        <div className="term" hidden={!activeTimer}>
          {term}
        </div>
      </div>
    </div>
  );
};

export default App;
