import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import style from "./Timer.scss";

const Timer = () => {
  let { activeTimer, minute } = useSelector(state => state);
  minute = minute ? minute : 30;
  let [count, setCount] = useState(0);
  useEffect(() => {
    if (activeTimer) {
      setTimeout(() => {
        setCount(count + 1 / minute / 60);
      }, 1000);
    }
  });
  const strokeDashoffset = -style.mainCir * count;
  return (
    <div className="timer__container">
      <svg className="timer" viewBox="0 0 100 100">
        <circle
          className="timer__common timer__main"
          style={{ strokeDashoffset }}
        />
        <circle
          className="timer__common timer__scale timer__scale--1min"
          strokeDasharray={`${0.5} ${style.subCir / minute - 0.5}`}
        />
        <circle
          className="timer__common timer__scale timer__scale--10min"
          strokeDasharray={`${1} ${(style.subCir / minute) * 10 - 1}`}
        />
      </svg>
    </div>
  );
};

export default Timer;
