import { combineReducers } from "redux";
import {
  ACTIVATE_TIMER,
  UPDATE_TITLE,
  UPDATE_MINUTE,
  UPDATE_TERM
} from "../actions";

const activeTimer = (state = false, { type, payload }) => {
  switch (type) {
    case ACTIVATE_TIMER:
      return payload;
    default:
      return state;
  }
};

const title = (state = "", { type, payload }) => {
  switch (type) {
    case UPDATE_TITLE:
      return payload;
    default:
      return state;
  }
};
const minute = (state = "", { type, payload }) => {
  switch (type) {
    case UPDATE_MINUTE:
      return payload;
    default:
      return state;
  }
};
const term = (state = "", { type, payload }) => {
  switch (type) {
    case UPDATE_TERM:
      return payload;
    default:
      return state;
  }
};
export default combineReducers({
  activeTimer,
  term,
  title,
  minute
});
